<template>
    <div class="musicListView">
        <MusicListViewTop :playlist="state.playlist"/>
        <PlayList :playList="state.playlist"/> 
    </div>
</template>

<script>
import { getMusicListDetail } from "@/api"
import { onMounted, reactive } from '@vue/runtime-core'
import { useRoute } from 'vue-router'
import MusicListViewTop from "../components/MusicListViewTop.vue"
import PlayList from "../components/PlayList.vue"
import store from "@/store"

export default {
    components:{
        MusicListViewTop,
        PlayList
    },
    setup(){
        const route = useRoute()
        let state = reactive({list:[],playlist:{
            creator:{},
            tracks:[]
        }})
        onMounted(async () => {
            let id = route.query.id
            let res = await getMusicListDetail(id)
            state.playlist = res.data.playlist
            store.commit('setPlaylist', res.data.playlist.tracks)
            store.commit('setPlayIndex', 0)
            clearInterval(store.state.intervalId)
            // store.commit('setPlayIcon',store.state.audio)
        })
        return {
            state
        }
    }    
}
</script>

<style scoped>
.musicListView{
    padding-bottom: 1.5rem;
}
</style>
