<template>
    <div class="musicListViewTop">
        <div class="musicListViewTopNav">
            <div class="left">
                <i class="back iconfont icon-zuojiantou" @click="$router.back()"></i>
                <span class="title">歌单</span>
            </div>
            <div class="right">
                <i class="iconfont icon-sousuo"></i>
                <i class="iconfont icon-youcecaidan"></i>
            </div>
        </div>
        <img class="bg" :src="playlist.coverImgUrl" alt="">
        <div class="description">
            <div class="descriptionLeft">
                <img :src="playlist.coverImgUrl">
                <span>
                    <i class="iconfont icon-24gl-play"></i>
                    {{transferNum(playlist.playCount)}}
                </span>
            </div>
            <div class="descriptionRight">
                <h3>{{playlist.name}}</h3>
                <div class="creator">
                    <img :src="playlist.creator.avatarUrl">
                    <span>{{playlist.creator.nickname}}</span>
                </div>
                <div class="descriptionContent">
                    {{playlist.description}}
                </div>
            </div>
        </div>
        <div class="iconList">
            <div class="iconItem">
                <i class="iconfont icon-pinglun"></i>
                <span>{{playlist.commentCount}}</span>
            </div>
            <div class="iconItem">
                <i class="iconfont icon-fenxiang"></i>
                <span>{{playlist.shareCount}}</span>
            </div>
            <div class="iconItem">
                <i class="iconfont icon-xiazai"></i>
                <span>下载</span>
            </div>
            <div class="iconItem">
                <i class="iconfont icon-xuanze"></i>
                <span>多选</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'MusicListViewTop',
    props: ['playlist'],
    setup(){
        function transferNum(num){
            let res = num
            if(num >= 100000000 ){
                res = num/100000000
                res = res.toFixed(2) + "亿"
            }else if(num >= 10000){
                res = num/10000
                res = res.toFixed(2) + "万"
            }
            return res
        }
        return {
            transferNum
        }
    }
}
</script>

<style lang="less" scoped>
.musicListViewTop{
    width: 7.5rem;
    padding: 0 0.2rem;
    .bg{
        position: fixed;
        left: 0;
        top: 0;
        width: 7.5rem;
        z-index: -999;
        filter: blur(40px);
    }
    .musicListViewTopNav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 0.8rem;
        font-size: 0.4rem;
        margin-top: .1rem;
        .left,.right{
            width: 1.7rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            color: #fff;
            .iconfont{
                font-size: .4rem;
            }
        }
        .back{
            padding: .1rem;
        }
    }
    .description{
        display: flex;
        justify-content: space-between;
        padding: .5rem 0;
        .descriptionLeft{
            position: relative;
            img{
                width: 3.1rem;
                border-radius: 0.1rem;
            }
            span{
                position: absolute;
                top: 0.1rem;
                right: 0.1rem;
                font-size: .25rem;
                color: rgb(255, 255, 255);
            }
        }
        .descriptionRight{
            width: 3.5rem;
            h3{
                color: #fff;
            }
            .creator{
                display: flex;
                align-items: center;
                margin: 0.2rem 0;
                img{
                    width: 0.6rem;
                    border-radius: 50%;
                    margin-right: .2rem;
                }
                span{
                    color:#ccc;
                    font-size: 0.3rem;
                }
            }
            .descriptionContent{
                font-size: 0.25rem;
                color: #ccc;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }
        }
    }
    .iconList{
        display: flex;
        justify-content: space-around;
        .iconItem{
            display: flex;
            flex-direction: column;
            align-items: center;
            .iconfont{
                font-size: 0.7rem;
                color: #fff;
            }
            span{
                font-size: 0.28rem;
                color: #fff;
                margin: .2rem 0;
            }
        }
    }
}
</style>