<template>
    <div class="playlist">
        <div class="playlistTop">
            <div class="left">
                <i class="iconfont icon-bofang"></i>
                <div class="text">
                    <span class="title">播放全部</span>
                    <span class="num">(共{{playList.tracks.length}}首)</span>
                </div>
            </div>
            <div class="shoucang">
                + 收藏({{transferNum(playList.subscribedCount)}})
            </div>
        </div>
        <div class="list">
            <div class="playItem" v-for="(item, i) in playList.tracks" :key="i">
                <div class="left" @click="setPlayIndex(i), setLyric(), play()">
                    <div class="index">{{i+1}}</div>
                    <div class="content">
                        <span class="title">{{item.name}}</span>
                        <span class="author">{{item.ar[0].name}} - {{item.al.name}}</span>
                    </div>
                </div>
                <div class="right">
                    <i class="iconfont icon-caozuo-bofang"></i>
                    <i class="iconfont icon-youcecaidan"></i>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapMutations,mapState, mapActions } from "vuex"
import { getCurrentInstance } from 'vue'

export default {
    props: ['playList'],
    computed:{
        ...mapState(['playlist', 'playCurrentIndex'])
    },
    methods: {
        ...mapMutations(['setPlayIndex']),
        ...mapActions(['reqLyric']),
        setLyric(){
            this.reqLyric(this.playlist[this.playCurrentIndex])
        }
    },
    updated(){
        this.reqLyric(this.playlist[this.playCurrentIndex])
    },
    setup(){
        function transferNum(num){
            let res = num
            if(num >= 100000000 ){
                res = num/100000000
                res = res.toFixed(2) + "亿"
            }else if(num >= 10000){
                res = num/10000
                res = res.toFixed(2) + "万"
            }
            return res
        }

        const { proxy } = getCurrentInstance()
        function play(){
            proxy.$EventBus.emit('playMusic')
        }
        return{
            transferNum,
            play
        }
    }
}
</script>

<style lang="less" scoped>
.playlist{
    width: 7.5rem;
    padding: 0 0.2rem;
    background-color: #fff;
    border-top-left-radius: 0.3rem;
    border-top-right-radius: 0.3rem;
    margin-top: 0.4rem;
    .playlistTop{
        display: flex;
        justify-content: space-between;
        height: 1rem;
        align-items: center;
        .left{
            display: flex;
            align-items: center;
            justify-content: space-between;
            width: 3.15rem;
            .iconfont{
                font-size: 0.45rem;
            }
            .title{
                font-weight: 900;
            }
            .num{
                font-size: 0.3rem;
                color: #373737;
            }
            .text{
                display: flex;
                align-items: center;
            }
        }
        .shoucang{
            font-size: 0.3rem;
            color: #fff;
            background: orangered;
            padding: 0.2rem;
            border-radius: 0.4rem;
        }
    }
    .list{
        .playItem{
            display: flex;
            justify-content: space-between;
            align-items: center;
            .left{
                display: flex;
                align-items: center;
                color: #666;
                .index{
                    width: 0.6rem;
                }
                .content{
                    margin-left: 0.1rem;
                    display: flex;
                    flex-direction: column;
                    width: 5rem;
                    padding: .1rem 0;
                    .title{
                        color: #000;
                    }
                    .author{
                        font-size: 0.2rem;
                    }
                }
            }
            .right{
                display: flex;
                width: 1rem;
                justify-content: space-between;
                .iconfont{
                    font-size: .4rem;
                }
            }
        }
    }
}
</style>